.videos-animations-container video{
  box-shadow: 7px 57px 30px rgba(0, 0, 0, 0.25);
  object-fit: cover;
  overflow: hidden;
  filter: grayscale(100%);
}

.video-animations-one {
  width: 32vw;
  margin-bottom: 40px;
}

.video-animations-two {
  width: 38.4vw;
}

.video-animations-three {
  width:29vw;
}

.video-one-size {
  width: 32vw;
  height: 18vw;
}

.video-three-size {
  width: 29vw;
  height: 16.3vw;
}

.video-two-size {
  width: 38.4vw ;
  height: 21.6vw ;
}

.video-description {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: right;
  letter-spacing: 0.14em;
  color: #FFFFFF;
}

.videos-animations-container video:hover{
  filter: grayscale(100%);
}

.video-animations-one{
  position: absolute;
  transform: rotate(8deg);
  margin-right: 10vw;
  margin-bottom: 8vw;
  z-index: 90;
}

.video-animations-one-div {
  z-index: 90;
  width: 100%;
  height: 100%;
}

.video-animations-two{
  position: absolute;
  transform: rotate(5deg);
  left:8vw;
  margin-top: 20vw;
  z-index: 50;
}

.video-animations-three{
  position: absolute;
  transform: rotate(-8deg);
  /* margin-left: 57vw; */
  right:6vw;
  margin-top: 30vw;
  z-index: 50;
}

.moon-second-section{
  position: absolute;
  margin-left: 28vw ;
  width: 37vw;
  z-index: 8;
}

.videos-animations-container {
  margin-top: 13vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 20%;
}

.videos-animations-container > .middle-text, .videos-animations-container > .bottom-text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 1.8vw;
  line-height: 125.9%;

  text-align: center;
  letter-spacing: 0.14em;


  color: #FFFFFF;
}
.videos-animations-container .middle-text {
  margin-top: 42vw;
}

.videos-animations-container > .bottom-text {
  margin-top: 40vw;

}

.video-description {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 1.1vw;
  line-height: 125.9%;

  text-align: center;
  letter-spacing: 0.14em;


  color: #FFFFFF;
}

.b12-left-bubble{
  position: absolute;
  width: 26vw;
  margin-top: 4vw;
  margin-right: 12vw;
}

.b12-orbit-container{
  position: absolute;
  left: -11.5vw;
  margin-top: 60vw;
}

.c-right-bubble{
  position: absolute;
  width: 16vw;
  margin-bottom: 3vw;
  margin-left: 2vw;
}

.c-orbit-container{
  position: absolute;
  right: -7vw;
  margin-top: 50vw;

}


.bottom-videos-container {
  display: flex;
  justify-content: center;
  margin-bottom: 300px;
}


@media (max-width: 900px) {
  .b12-orbit-container, .c-orbit-container, .moon-second-section{
    display: none;
  }

  .mobile-video {
    width: 55vw;
    height: 34vw;
  }

  .video-animations-two{
    margin-top: 40vw;
    left: -13vw;
  }
  
  .video-animations-three{
    right: -20vw;
  }

  .videos-animations-container > .middle-text, .videos-animations-container > .bottom-text {
    font-size: 3vw;
  }

  .videos-animations-container .bottom-text {
    margin-top: 85vw;
    padding-bottom: 300px;
  }
  .videos-animations-container > .middle-text {
    margin-top: 65vw !important;
  }

  .video-animations-one{

    margin-right: 50vw;
    margin-top: 5vw;

  }
}